



































































































import { Component, Prop, PropSync, Vue, Watch } from 'vue-property-decorator';
import { SelectItem, TableMenuVO } from '@/common/types';
import {
  deleteDataApi,
  getCodeListApi,
  patchDataApi,
  postDataApi,
} from '@/utils/apis';
import _ from 'lodash';
import { confirmDelete } from '@/utils/alerts';

interface MenuVO extends TableMenuVO {
  level: number;
}

@Component({
  name: 'MenuEditDialog',
})
export default class extends Vue {
  @PropSync('dialog', { required: true, type: Boolean }) syncedDialog!: boolean;
  @Prop({ required: true }) readonly editItem!: MenuVO;
  @Prop({ required: true }) readonly mode!: string | null;

  loading: boolean = false;
  MENU_TYPE: SelectItem[] | null = null;

  async mounted() {
    this.MENU_TYPE = await getCodeListApi(`MENU_TYPE`);
  }

  @Watch('dialog')
  watchDialog(val: boolean) {
    if (val) {
      this.$refs.observer && (this.$refs.observer as any).reset();
    } else {
      this.$storage.remove('drawer');
    }
  }

  async save() {
    const isValid = await (this.$refs.observer as any).validate();
    if (!isValid) {
      return;
    }
    this.mode === '수정' ? this.patch() : this.create();
  }

  async create() {
    this.loading = true;
    const response = await postDataApi<TableMenuVO>(
      `admin/menus/`,
      this.editItem,
    );
    this.loading = false;
    if (_.startsWith(response.code, `S`)) {
      this.syncedDialog = false;
      this.$emit('finished');
    }
  }

  async patch() {
    this.loading = true;
    const response = await patchDataApi<TableMenuVO>(
      `admin/menus/`,
      this.editItem,
      this.editItem.id!,
    );
    this.loading = false;
    if (_.startsWith(response.code, `S`)) {
      this.syncedDialog = false;
      this.$emit('finished');
    }
  }

  async delete() {
    const result = await confirmDelete();
    if (result.value) {
      this.loading = true;
      const response = await deleteDataApi<TableMenuVO>(
        `admin/menus/`,
        this.editItem.id!,
      );
      this.loading = false;
      if (_.startsWith(response.code, `S`)) {
        this.$emit('finished');
      }
    }
  }

  filterMenuType(item: MenuVO) {
    if (item.parentId !== 1) {
      return this.MENU_TYPE!.filter((item) => item.value !== 'G');
    }
    return this.MENU_TYPE;
  }
}
