





























































































import { Component, Vue } from 'vue-property-decorator';
import { DataTableHeader, SelectItem, TableMenuVO } from '@/common/types';
import { getCodeListApi, getListApi } from '@/utils/apis';
import envs from '@/constants/envs';
import MenuEditDialog from '@/views/admin/menu/components/MenuEditDialog.vue';
import ButtonSet from '@/components/speeddial/ButtonSet.vue';

interface MenuVO extends TableMenuVO {
  level: number;
}

@Component({
  name: 'MenuList',
  components: {
    ButtonSet,
    MenuEditDialog,
  },
})
export default class extends Vue {
  readonly envs: typeof envs = envs;
  mode: string | null = null;
  items: TableMenuVO[] = [];
  editItem: TableMenuVO = Object.create(null);
  selected: TableMenuVO[] = [];
  loading: boolean = false;
  dialog: boolean = false;

  MENU_TYPE: SelectItem[] | null = null;

  headers: DataTableHeader[] = [
    {
      text: `타입`,
      align: `center`,
      value: `type`,
      filterType: 'select',
      filterSelectItem: [],
      width: 100,
    },
    {
      text: `메뉴명`,
      align: `start`,
      value: `name`,
    },
    {
      text: `메뉴 순서`,
      align: `end`,
      value: `displayOrder`,
      width: 100,
    },
    {
      text: `Action`,
      align: `center`,
      value: `action`,
      filterable: false,
      width: 250,
    },
    {
      text: `작업 일시`,
      align: `center`,
      value: `updated`,
      filterable: false,
      width: 160,
    },
    {
      text: `작업자`,
      align: `start`,
      value: `updatedBy`,
      filterable: false,
      width: 100,
    },
  ];

  async mounted() {
    this.MENU_TYPE = await getCodeListApi(`MENU_TYPE`);
    this.headers[0].filterSelectItem = this.MENU_TYPE;
    this.getList();
  }

  async getList() {
    this.selected = [];
    this.items = [];
    this.loading = true;
    const response = await getListApi<MenuVO[]>(`admin/menus/`);
    this.loading = false;
    this.items = response.data || [];
  }
}
